.ripple {
  position: absolute;
  z-index: 99999999;
  background: rgba(0, 0, 0, 0.15);
  border-radius: 100%;
  -webkit-transform: scale(0);
      -ms-transform: scale(0);
          transform: scale(0);
  pointer-events: none;
  -webkit-animation: ripple 0.75s ease-out;
          animation: ripple 0.75s ease-out;
}

@-webkit-keyframes ripple {
  to {
    -webkit-transform: scale(2);
            transform: scale(2);
    opacity: 0;
  }
}

@keyframes ripple {
  to {
    -webkit-transform: scale(2);
            transform: scale(2);
    opacity: 0;
  }
}



